<template>
    <div class="container">
      <h1 class="text-center">Weather</h1>
      <div class="row">
        <div class="col-md-6 offset-md-3">
          <form @submit.prevent="getWeather">
            <div class="form-group">
              <label for="city">City</label>
              <input type="text" class="form-control" id="city" v-model="city" required>
            </div>
            <button type="submit" class="btn btn-primary">Get Weather</button>
          </form>
        </div>
      </div>
      <div class="row" v-if="data">
        <div class="col-md-6 offset-md-3">
          <div class="card">
            <div class="card-body">
  
              <p class="card-text">{{ data.data.weather[0].maxtempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[0].mintempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[0].hourly[0].weatherDesc[0].value }};</p>
  
              <p class="card-text">{{ data.data.weather[1].maxtempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[1].mintempC }} &#8451;</p>
  
              <p class="card-text">{{ data.data.weather[2].maxtempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[2].mintempC }} &#8451;</p>
              
              <p class="card-text">{{ data.data.weather[3].maxtempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[3].mintempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[3].hourly[0].weatherDesc[0].value }};</p>
              
              <p class="card-text">{{ data.data.weather[3].maxtempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[3].mintempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[3].hourly[0].weatherDesc[0].value }};</p>
              
              <p class="card-text">{{ data.data.weather[3].maxtempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[3].mintempC }} &#8451;</p>
              <p class="card-text">{{ data.data.weather[3].hourly[0].weatherDesc[0].value }};</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        city: '',
        data: null,
      }
    },
    methods: {
      async getWeather() {
        try {
          let base_url = "http://api.worldweatheronline.com/premium/v1/weather.ashx?key=169ce696d0924eb38ec80224232701&q="
          let city_url = base_url.concat(this.city);
          let final_url = city_url.concat("+&num_of_days=5&format=json");
          console.log(final_url);
          const response = await fetch(final_url)
          
          //const response = await fetch("http://api.worldweatheronline.com/premium/v1/weather.ashx?key=169ce696d0924eb38ec80224232701&q=Ankara+&num_of_days=3&format=json");
          this.data = await response.json();
  
        } catch (error) {
          console.error(error);
        }
      }
    },
  
  
  }
  </script>